@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 70%;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  padding: 10px 0;
  background: linear-gradient(90deg, #000, #f00);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  z-index: 2;
}

.navbar a {
  border: none;
  background: none;
  color: #fff;
  cursor: pointer;
  font-variant: small-caps;
  font-size: 1.1rem;
  text-decoration: none;
  margin-right: 1vw;
}

.navbar a:hover {
  color: #eee;
}

.top-bar {
  display: flex;
  flex-direction: row;
}

.car {
  width: 56%;
  padding-top: 23%;
  background: center/contain no-repeat url('./images/car01.jpg');
}

.sim-race {
  position: relative;
  z-index: 1;
  width: 44%;
  padding-top: 23%;
  background: center/contain no-repeat url('./images/simRaceTitle.jpg');
}

.mid-bar {
  width: 100%;
  padding-top: 53%;
  background: center/contain no-repeat url('./images/bestSim.jpg');
  margin-top: 5rem;
}

.bot-bar-pre {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 6rem;
}

.bot-bar {
  position: relative;
  width: 100%;
  padding-top: 85%;
  background: center/contain no-repeat url('./images/bottomImage.jpg');
}

.bot-bar-content {
  color: #222;
  font-size: 1.5vw;
  bottom: 11%;
  width: 45vw;
  left: 0;
  right: 0;
  position: absolute;
  margin: 0 auto;
}

.bot-bar-content p {
  margin: 1vw 0;
}

.bot-bar-content span {
  font-size: 2vw;
  font-weight: bolder;
}

.bot-bar-content button {
  margin-top: .5rem;
}

.reg-btn {
  font-size: 1.4vw;
  color: #fff;
  padding: 5px 20px;
  font-variant: small-caps;
  border: none;
  cursor: pointer;
  border-radius: 10px;
  background: linear-gradient(90deg, rgb(53, 19, 19), rgb(139, 1, 1));
  animation: btn-animation 4s infinite;
}

.top-bar-btn {
  bottom: 7%;
  width: 200px;
  left: 0;
  right: 0;
  position: absolute;
  margin: 0 auto;
}

.reg-btn:hover {
  animation: btn-beat 1s infinite;
}

@keyframes btn-animation {
  5%,
  50% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.95);
  }
  15% {
    transform: scale(1.05);
  }
  20% {
    transform: scale(1.05) rotate(-5deg);
  }
  25% {
    transform: scale(1.05) rotate(5deg);
  }
  30% {
    transform: scale(1.05) rotate(-3deg);
  }
  35% {
    transform: scale(1.05) rotate(2deg);
  }
  40% {
    transform: scale(1.05) rotate(0);
  }
}

@keyframes btn-beat {
  50% {
    transform: scale(1.1);
  }
}

.rules ul {
  list-style: none;
}

.rules li {
  list-style: none;
}

.rules li {
  margin-top: 2rem;
}

.footer {
  height: 100px;
  background: linear-gradient(90deg, black, red);
  margin-top: -100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #eee;
  padding: 3rem
}

.wrapper {
  padding-top: 7rem;
  min-height: 100vh;
  padding-bottom: 200px;
}

dl {
  width: 400px;
  display: grid;
  grid-template-columns: max-content auto;
}

dt {
  grid-column-start: 1;
}

dd {
  margin-left: auto;
  grid-column-start: 2;
}

.prizes {
  font-size: 1.3vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  width: 40%;
  background: #000;
  color: #fff;
  margin-right: 2rem;
  border-radius: 30px;
}

.prizes h3 {
  padding-top: 0.3rem;
  margin-top: 0.8rem;
  margin-bottom: 0.3rem;
  text-align: center;
  border-top: 2px solid #fff;
}

.main-prize {
  margin-top: 2rem;
  padding-right: 2rem;
  text-align: right;
  font-size: 1.5rem;
  font-weight: bold;
}

.main-prize-content {
  background: linear-gradient(67deg, #000, #f00);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: bolder;
  font-size: 3rem;
  margin-left: 1rem;
}

.rules-pre {
  padding: 2rem;
  color: #222;
}

.only-mobile {
  display: none;
}


@media (max-width: 1280px) {
  body {
    font-size: 16px;
  }

  .wrapper {
    padding-top: 5rem;
    padding-bottom: 130px;
  }

  .navbar a {
    font-size: 3vw;
  }

  .top-bar {
    flex-direction: column;
    position: inherit;
  }

  .top-bar-btn {
    font-size: 1rem;
    width: 150px;
    position: inherit;
    margin-top: 1.5rem;
  }

  .car {
    padding-top: 40%;
    width: 100%;
  }

  .sim-race {
    padding-top: 40%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .bot-bar-pre {
    flex-direction: column;
  }

  .mid-bar {
    margin-top: 4rem;
  }

  .bot-bar-pre {
    margin-top: 0;
  }

  .main-prize {
    padding: 0;
    text-align: center;
  }

  .main-prize-content {
    display: block;
  }

  .prizes {
    padding: 1rem;
    font-size: 1rem;
    width: 90%;
    margin-right: 2rem;
    margin-left: 2rem;
  }

  .prizes dl {
    width: 80vw;
  }

  .bot-bar {
    margin-top: 2rem;
  }

  .bot-bar-content button {
    display: none;
  }

  .footer {
    font-size: 2.5vw;
  }

  .only-mobile {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
  }

  .only-mobile button {
    font-size: 1rem;
  }
}